(function() {
  'use strict';

 
angular
  .module('gpSocial')
  .directive('checkAgeDirective', checkAgeDirective);
  
  function checkAgeDirective(){
    return {
      require: 'ngModel',
      link: function (scope, element, attributes, ctrl) {
          ctrl.$validators.checkAgeDirective = function (modelValue) {
              scope.ageError = true;
              if (ctrl.$isEmpty(modelValue)) // if empty, correct value
              {   
                  scope.ageError = false;
                  scope.ageErrorText = 'Age should exist';
                  return scope.ageError;
              }
              if (modelValue >= 3 && modelValue <= 150){ // correct value
                  scope.ageError =  true;
                  scope.ageErrorText = 'Your Age should be between 3 and 150 years';
                  return scope.ageError;
              }else{
                scope.ageError = false;
                return scope.ageError;
              }
          };
      }
    };
  }



})();
(function() {
  'use strict';

  angular
    .module('gpSocial')
    .controller('NavbarController', NavbarController);

  /** @ngInject */
  function NavbarController($mdSidenav, $log, navbarMenu, profileService) {
    var vm = this;

    vm.toggleLeft = buildToggler('left');
    vm.menuSections = navbarMenu;

    profileService.getProfile().then(function(user) {
      vm.user = user;
    });

    function buildToggler(navID) {
      return function() {
        $mdSidenav(navID)
          .toggle()
          .then(function () {
            $log.debug("toggle " + navID + " is done");
          });
      }
    }
  }
})();
(function () {
  'use strict';

  angular
    .module('gpSocial')
    .run([ "$httpBackend","messagesDataService","historyDataService", backend]);

  // Mock a server side, return data.
  function backend($httpBackend, messagesDataService, historyDataService) {

    // $httpBackend.whenGET('app/messages/messages.html').respond(getAllUserMessages);
    // function getAllUserMessages(method, url, data) {
    //   var userMessages = messagesDataService.getAll();
    //   console.log("from data service: " + userMessages[0].title);
    //   return [200, userMessages, {}];
    // }

    $httpBackend.whenGET('/fakeRemoteServer/userMassages').respond(getAllUserMessages);
    function getAllUserMessages() {
      var userMessages = messagesDataService.getAll();
      return [200, userMessages, {}];
    }

    $httpBackend.whenGET('/fakeRemoteServer/userHistory').respond(getAllUserHistory);
    function getAllUserHistory() {
      var userHistory = historyDataService.getAll();
      return [200, userHistory, {}];
    }
    // Ignore all other requests
    $httpBackend.whenGET(/.*/).passThrough();
  }

}());

